import axios from 'axios'
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    items: [],
    current: {
        name: '',
        region: '',
        xmlId: '',
        id: '',
        regionCode: '',
    },
    currentXmlId: '',
    currentCityIp: {},
}

// getters
const getters = {
    getByXmlId: state => id => {
        return [...state.items].find(city => city.xmlId === id)
    },
}

const actions = {
    getCities(context) {
        axios.get(process.env.VUE_APP_API_PATH+'/public/cities/',{
            headers: jsonHeader,
            withCredentials: true,
        }).then(response => {
            if(typeof response.data === 'object' && +response.data.error === 0) {
                context.commit('setItems', response.data.result);
                context.commit('setCurrentCity', response.data.result);
                //context.dispatch('getGeodata');
            }
        }).catch(function (error) {
        });
    },
    setCity(context, data) {
        if(data.city !== undefined) {
            context.commit('setCity', data.city);
            //context.dispatch('getGeodata',{reload:true});
        }
        context.commit('actions/hideMap', {}, { root: true });
        if(data.closePopup !== undefined) {
            context.commit('popups/close', {name: data.closePopup}, { root: true });
        }
        context.commit('actions/setPage', 1, { root: true });
        context.commit('actions/isLoadNextPage', true, { root: true });
        context.commit('actions/setCouponList', {items: []}, { root: true });
        context.commit('actions/setCategoriesList', {items:  new Set()}, { root: true });
        context.dispatch('actions/getCouponList', {currentSection: data.currentSection}, { root: true });
        context.dispatch('actions/getCategoriesList', {}, { root: true });
    },
    setCityCookies(context) {
        let currentCity = VueCookies.get('currentCity');
        if(currentCity !== null && currentCity.id > 0) {
            context.commit('setCity', currentCity);
        }
    },
    getGeodata(context, data) {
        if(!(state.current.coords !== undefined && state.current.coords.length > 0) || data.reload) {
            axios.get('https://geocode-maps.yandex.ru/1.x/?format=json&results=1&geocode='+state.current.name).then(resp=>{
                if(resp.data.response.GeoObjectCollection.featureMember){
                    let coords = resp.data.response.GeoObjectCollection.featureMember[0].GeoObject.Point.pos.split(' ')
                    context.commit('setCurrentCoords', [coords[1],coords[0]]);
                }
            }).catch(function (error) {
            })
        }
    }
}

const mutations = {
    setItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            let items = new Set();
            for(let itemIndex in data.items) {
                items.add(data.items[itemIndex]);
            }
            state.items = items;
        }
    },
    setCurrentCity(state, data) {
        if(typeof data === 'object' && data.current !== undefined) {
            if(state.current.id === '') {
                Vue.set(state.current,'name', data.current.name);
                Vue.set(state.current,'region', data.current.region);
                Vue.set(state.current,'xmlId', data.current.xmlId);
                Vue.set(state.current,'id', data.current.id);
                Vue.set(state.current,'regionCode', data.current.regionCode);
            }
            Vue.set(state,'currentCityIp', data.current);
            VueCookies.set('currentCityIp', data.current);
            let currentCity = VueCookies.get('currentCity');
            if(!currentCity) {
                VueCookies.set('currentCity', data.current);
            }
        }
    },
    setCity(state, city) {
        if(typeof city === 'object') {
            if(city.xmlId !== undefined && state.current.xmlId !== city.xmlId) {
                state.current.name = city.name;
                state.current.region = city.region;
                state.current.xmlId = city.xmlId;
                state.current.id = city.id;
                state.current.regionCode = city.regionCode;
                VueCookies.set('currentCity', city);
            }
        }
    },
    setCurrentCoords(state, coords) {
        state.current.coords = coords;
        VueCookies.set('currentCity', state.current);
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}