/**
 *     
 * */

import axios from 'axios'
/**      axios
 * qs.stringify({ id: data.id }),
 */
import qs from 'qs';
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    pageContent: [],
}

// getters
const getters = {
    getPageContent: state => id => {
        let item = state.pageContent.find(content => +content.id === +id);
        return item !== undefined ? item : {};
    },
}

const actions = {
    getPageContent(context, data) {
        if (typeof(data.pageContentID) !== 'undefined') {
            if(context.state.pageContent.length === 0 || context.state.pageContent.filter(content => +content.id === +data.pageContentID).length === 0){
                let query = {};
                query.ID = data.pageContentID;
                axios.get(process.env.VUE_APP_API_PATH+'/public/content/', {
                    headers: jsonHeader,
                    params: query,
                }).then(response => {
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setPageContent', response.data.result);
                    }
                }).catch(function (error) {
                });
            }
        }
    },
}

const mutations = {
    setPageContent(state, data) {
        state.pageContent.push(data.item)
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}