import axios from 'axios'
import {jsonHeader, wwwFormHeader, fileHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    current: {},
    isLoaded: false,
    sendForm: false,
    statistic: {},
    itemDetail: {}
}

// getters
const getters = {
    getByXmlId: state => id => {
        return [...state.items].find(city => city.xmlId === id)
    },
    getCurrent: state => id => {
        return state.current
    },
}

const actions = {
    getCurrent(context) {
        return new Promise((resolve, reject) => {
            context.commit('setLoaded', false);
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined){
                axios.get(process.env.VUE_APP_API_PATH+'/partner/current/',{
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                }).then(response => {
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setCurrent', response.data.result);
                        if(response.data.result.item && response.data.result.item.id) {
                            context.dispatch('setProviderData',response);
                        }

                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                    context.commit('setLoaded', true);
                    resolve();
                }).catch(function (error) {
                    reject();
                });
            } else {
                context.dispatch('auth/logout',{},{root:true})
                context.commit('setLoaded', true);
                resolve();
            }
        });
    },
    getStatistic(context) {
        return new Promise((resolve, reject) => {
            context.commit('setLoaded', false);
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined){
                axios.get(process.env.VUE_APP_API_PATH+'/partner/statistics/',{
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                }).then(response => {
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setProviderStatistic',response.data.result);

                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                    context.commit('setLoaded', true);
                    resolve();
                }).catch(function (error) {
                    reject();
                });
            } else {
                context.dispatch('auth/logout',{},{root:true})
                context.commit('setLoaded', true);
                resolve();
            }
        });
    },
    getByActionID(context,data) {
        return new Promise((resolve, reject) => {
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined){
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/partnerbyaction/'+data.action+'/',{
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                }).then(response => {
                    context.commit('setLoaded', true);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.dispatch('setProviderData',response);
                        resolve();
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    reject();
                });
            } else {
                context.dispatch('auth/logout',{},{root:true})
                context.commit('setLoaded', true);
                resolve();
            }
        });
    },
    getByID(context,data) {
        return new Promise((resolve, reject) => {
            if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined){
                localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                axios.get(process.env.VUE_APP_API_PATH+'/moderator/providers/'+data.id+'/',{
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        type: 'getEditData',
                    },
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    context.commit('setLoaded', true);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.dispatch('setProviderData',response);
                        resolve();
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                }).catch(function (error) {
                    reject();
                });
            } else {
                context.dispatch('auth/logout',{},{root:true})
                context.commit('setLoaded', true);
                resolve();
            }
        });
    },
    setProviderData(context,response){
        context.commit('setCurrent', response.data.result);
    },
    initDetail(context, data) {
        return new Promise((resolve, reject) => {
            if (!state.isLoad) {
                context.commit('setLoaded', true);
                axios.get(process.env.VUE_APP_API_PATH + '/public/supplier/' + data.id, {
                    withCredentials: true,
                    params: {
                        addViewCounterMobile: true
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    context.commit('setSupplier', response.data.result);
                    context.commit('setLoaded', false);
                    resolve();
                }).catch(function (error) {
                    context.commit('setLoaded', false);
                    resolve();
                });
            } else {
                resolve();
            }
        });
    },
}

const mutations = {
    setCurrent(state, data) {
        if(typeof data === 'object' && data.item !== undefined) {
            Vue.set(state,'current', data.item);
        }
    },
    setProviderStatistic(state, data) {
        if(typeof data === 'object' && data.statistic !== undefined) {
            Vue.set(state,'statistic', data.statistic);
        }
    },
    setCurrentField(state, data) {
        if(typeof data === 'object' && data.name !== undefined) {
            Vue.set(state.current, data.name, data.value);
        }
    },
    setLoaded(state, data) {
        Vue.set(state,'isLoaded', data);
    },
    setSend(state, data) {
        Vue.set(state,'sendForm', data);
    },
    partnerClear(state, data){
        state.current = {};
    },
    setSupplier(state, data) {
        Vue.set(state, 'itemDetail', data);
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}