/**
 *    
 * */

import axios from 'axios'
/**      axios
 * qs.stringify({ id: data.id }),
 */
import qs from 'qs';
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    items: [],
    isLoadNextPage: true,
    isLoad: false,
    positive: 0,
    negative: 0
}

// getters
const getters = {
    getList: state => id => {
        return state.data;
    },
}

const actions = {
    getList(context, data) {
        if(!state.isLoad && state.isLoadNextPage || data.reload){
            context.commit('setLoad', true);
            axios.get(process.env.VUE_APP_API_PATH+'/public/opinions/', {
                headers: jsonHeader,
                params: {
                    actionId: data.actionId
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                context.commit('setLoad', false);
                if(data.reload) {
                    context.commit('setData', {items: []});
                }
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setData', response.data.result);
                    context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    getListPartner(context, data) {
        if(!state.isLoad && state.isLoadNextPage || data.reload){
            context.commit('setLoad', true);
            axios.get(process.env.VUE_APP_API_PATH+'/partner/opinions/', {
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false}
            }).then(response => {
                context.commit('setLoad', false);
                if(data && data.reload) {
                    context.commit('setData', {items: []});
                }
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setData', response.data.result);
                    context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    sendAnswer(context, data) {
        return new Promise((resolve, reject) => {
            let formData = new FormData();

            formData.append('text', data.text);

            axios.post(process.env.VUE_APP_API_PATH+'/partner/opinions/' + data.id + '/', formData, {
                headers: {
                    'Content-Type': 'application/json;charset=utf-8',
                    'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                },
                withCredentials: true
            }).then(response => {
                resolve();
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    if (response.data.result !== undefined) {
                        context.commit('setAnswer', {answer: response.data.result.answer, id: data.id});
                    }
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                reject();
            });
        });
    },
    sendForm(context, data) {
        let params = {
            text: data.text,
            actionId: data.actionId,
            type: data.type
        };

        axios.post(process.env.VUE_APP_API_PATH+'/user/opinions/',{}, {
            headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            params: params,
            paramsSerializer: function(params) {
                return qs.stringify(params, {arrayFormat: 'repeat'})
            },
        }).then(response => {
            if (typeof response.data === 'object' && +response.data.error === 0) {
                if(response.data.result.item.id !== undefined){
                    context.commit('users/pushOpinion', { id:response.data.result.item.id, action: data.actionId },{ root:true });
                    let item = response.data.result.item;
                    item.text = data.text;
                    context.commit('pushItem',item);
                    context.commit('actions/incrementCouponDetailCountOpinion','',{root:true});
                }
            } else {
                if( +response.data.error===401){
                    context.dispatch('auth/logout',{},{root:true})
                }
            }
        }).catch(function (error) {
        });
    },
}

const mutations = {
    setData(state, data) {
        if(data !== null && data.items !== null) {
            if(state.page < 2) {
                state.items = [];
            }
            for (let itemIndex in data.items) {
                if(data.items.hasOwnProperty(itemIndex)) {
                    let item = data.items[itemIndex];
                    let itemFind = state.items.find(opinion => +opinion.id === +item.id);
                    if(!itemFind){
                        state.items.push(item);
                    }
                }
            }
            state.positive = data.positive;
            state.negative = data.negative;
        }
    },
    clearData(state, data) {
        state.items = [];
        state.positive = 0;
        state.negative = 0;
    },
    pushItem(state, data) {
        state.items.push(data);
    },
    setStatus(state, data) {
        Vue.set(state, 'status', !data.error);
    },
    setPage(state, data) {
        state.page = data;
    },
    incrementPage(state) {
        state.page++;
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setButtonState(state, data) {
        for (let itemIndex in state.items) {
            if(+data.id === +state.items[itemIndex].id) {
                if(state.items[itemIndex].form.btnType !== undefined) {
                    Vue.set(state.items[itemIndex].form, 'btnType', data.state);
                }
            }
        }
    },
    toggleForm(state, data) {
        for (let itemIndex in state.items) {
            if(+data.id === +state.items[itemIndex].id) {
                Vue.set(state.items[itemIndex].form, 'show', !state.items[itemIndex].form.show);
            }
        }
    },
    setError(state, data) {
        for (let itemIndex in state.items) {
            if(+data.id === +state.items[itemIndex].id) {
                Vue.set(state.items[itemIndex].form, 'commentError', data.error);
            }
        }
    },
    setComment(state, data) {
        for (let itemIndex in state.items) {
            if(+data.id === +state.items[itemIndex].id) {
                Vue.set(state.items[itemIndex].form, 'comment', data.value);
            }
        }
    },
    setAnswer(state, data) {
        for (let itemIndex in state.items) {
            if(+data.id === +state.items[itemIndex].id) {
                Vue.set(state.items[itemIndex], 'answer', data.answer);
            }
        }
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}