<template>
  <div class="modal-block" :class="[{'open': show}, addClass]">
    <div class="black-bg" @click="close"></div>
      <div class="modal" :show="show">
          <div class="modal-info">
            <div v-if="modalType == ''">
              <!--   red  green  modal-ttl  modal-txt-->
              <div :class="{'modal-ttl': true, 'red': redtitle, 'green': greentitle }" v-if="title">{{title}}</div>
              <div :class="{'modal-txt': true, 'red': redtxt, 'green': greentxt }" v-if="text">
                <p>{{text}}</p>
              </div>
              <slot></slot>
            </div>
            <div class="long-margin" v-if="modalType == 'notOrder'">
                  <!--   red  green  modal-ttl  modal-txt-->
                  <div class="modal-ttl red">      <br>,   </div>
                  <div class="modal-txt">
                    <p>    <br>,   <br>    <br>  </p>
                    <p>P.S.     <br><a href="javascript:void(0)" @click="showPopup('mailZems');close()"></a>  <br> . </p>
                  </div>
            </div>
            <div class="long-margin" v-if="modalType == 'isSend'">
                  <!--   red  green  modal-ttl  modal-txt-->
                  <div class="modal-ttl red">    <br>  ,   <br>  </div>
                  <div class="modal-txt">
                    <p>    <br>    <br>   .</p>
                    <p>P.S.     <br><a href="javascript:void(0)" @click="showPopup('mailZems');close()"></a>  <br> . </p>
                  </div>
            </div>
          </div>
          <div class="modal-butt">
            <div class="ok-butt" v-if="name == 'cooperationAccepted'" @click="closeCooperationAccepted">{{btntext}}</div>
            <div class="ok-butt" v-else @click="close">{{btntext}}</div>
          </div>
        </div>
    </div>
</template>

<script>
  import {mapState} from 'vuex';
  import {number_format} from '@/helpers/formatted';
    export default {
        name: "PopupInfoBlock",
        props: {
            width: {
                default: function () {
                    return 320
                }
            },
            addClass: {default: ''},
            show: {
                default: function () {
                    return false
                }
            },
            isSetTop: {
                default: function () {
                    return false
                }
            },
            isBtnClose: {
                default: function () {
                    return true
                }
            },
            isSetStyled: {
                default: function () {
                    return false
                }
            },
            isSetBlank: {
                default: function () {
                    return false
                }
            },
            isResize: {
                default: function () {
                    return false
                }
            },
            name: '',
            title: {
                default: ''
            },
            text: {
                default: ''
            },
            modalType: {
                default: ''
            },
            btntext: {
                default: ''
            },
            redtitle:{
              default: function(){
                return false
              }
            },
            redtxt:{
              default: function(){
                return false
              }
            },
            greentitle: {
              default: function(){
                return false
              }
            },
            greentxt:{
              default: function(){
                return false
              }
            },
            average: {
                default: function () {
                    return ''
                }
            },
            discount: {
                default: function () {
                    return ''
                }
            },
            projectDiscount: {
                default: function () {
                    return ''
                }
            },
        },
        watch: {
            show: function () {
                if (this.show) {
                    if (this.isSetTop) {
                        this.updateViewportSize();
                    }
                    document.getElementsByTagName('body')[0].classList.add("nonscroll");
                }
            },
            isResize: function (newV, oldQV) {
                if (this.isSetTop && (newV !== oldQV)) {
                    this.updateViewportSize();
                }
            },
        },
        data: function () {
            return {
                top: 20,
            }
        },
        computed: {
            ...mapState({
              user: state => state.users.user,
              options: state => state.options.items,
              formAuthFields: state => state.auth.forms.auth.fields,
            }),
            discountSumRetail: function () {
                let discountSum = this.average * this.discount / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSumUser: function () {
                let discountSum = this.average * (this.projectDiscount - this.discount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
            discountSum: function () {
                let discountSum = this.average * (this.projectDiscount - this.options.ourDiscount) / 100;
                return number_format(discountSum > 0 ? discountSum : 0, 0, '', ' ')
            },
        },
        mounted: function () {
            if (this.isSetTop) {
                this.topListener();
            }
        },
        methods: {
          number_format: function (v) {
              return number_format(v, 0, '', ' ')
          },
            updateViewportSize: function () {
                this.$nextTick(function () {
                    if (typeof(this.$refs.overlayPopup) !== 'undefined') {
                        let top = this.getViewportSize().h - this.$refs.overlayPopup.clientHeight;
                        if (top < 1) {
                            top = 1
                        }
                        this.top = (top / 2);
                    }
                });
            },
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
            updatePopupStyled: function () {
                this.$nextTick(function () {
                    this.$refs.overlayPopup.classList.add('popup--styled')
                })
            },
            topListener: function () {
                let self = this;
                this.$nextTick(function () {
                    self.updateViewportSize();
                    window.addEventListener('resize', self.updateViewportSize);
                });

            },

            getViewportSize: function () {
                let d = window.document.querySelector("html");
                return {w: d.clientWidth, h: d.clientHeight};
            },

            close: function () {
                if(this.show && this.name){
                    this.$store.commit('popups/close', {name: this.name});
                    this.$emit('close');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                } else {
                    this.$emit('close');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                }
                this.$store.commit('auth/setFormAuthError',{ name: this.formAuthFields.login.name, value: false });
            },
            closeCooperationAccepted: function () {
                if(this.show && this.name){
                    this.$store.commit('popups/close', {name: this.name});
                    this.$emit('close');
                    this.$router.push('/');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                } else {
                    this.$emit('close');
                    this.$router.push('/');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                }
            }
        }
    }
</script>

<style scoped>
    .modal-block{
        width: 320px;
        overflow: hidden;
        height: 100vh !important;
        height: stretch;
        overflow-y: auto;
        min-height: stretch;
        position: fixed;
        background-color: rgba(0,0,0,0.5);
        z-index: 90;
        align-items: center;
        left: 0;
        top: 0;
        opacity: 0;
        display: -webkit-box;
        transition: all .15s;
        pointer-events: none;
        justify-content: center;
    }
    .modal-block .modal{
      transform: scale(0.5);
      transition: all .1s;
    }
    .modal-block .black-bg{
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 0;
      left: 0;
      background-color: transparent
    }
    .modal-block.open{
      opacity: 1;
      pointer-events: all;
    }
    .modal-block.open .modal{
      transform: scale(1);
    }
    .modal-howCalculated .modal-ttl, .modal-howCalculated .modal-txt p{
      text-align: left !important;
    }
</style>
