<template>
    <div class="">
        <div class="search-inpt-bg" :class="{fadeIn: searchFocus}" @click="blurInput"></div>
        <div class="search" :class="{focused: searchFocus, opened: showSearch, filled: searchQuery.length > 0 && searchFocus}">
            <input-styled
                    v-model="searchQuery"
                    :remove-btn="true"
                    :current-model="searchQuery"
                    :placeholder="' '"
                    @enter="getSearch($event)"
                    @focus="focusInput()"
                    :classBtn="'search'"
                    :type="'search'"
                    :placeholder-input="' '"
                    :is-clear-btn="true"></input-styled>
            <a href="javascript:void(0);" class="text-color-white" v-if="searchQuery.length > 0 && searchFocus" @click="blurInput"></a>
        </div>
    </div>
</template>

<script>

    import { mapState } from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue';

    export default {
        name: "SearchTop",
        data(){
            return {
                focus: false,
                searchFocus: false,
            }
        },
        props: {
                showSearch: {
                    default: function(){
                        return false
                    }
                }
        },
        components: {
                InputStyled,
        },
        computed: {
            searchQuery: {
                get () {
                    return this.$store.state.search.query
                },
                set (value) {
                    this.$store.commit('search/updateQuery', value)
                }
            },
            ...mapState({
                currentCity:  state => state.cities.current,
            }),
        },
        methods: {
            focusInput(){
                this.searchFocus = true;
                document.getElementsByTagName('body')[0].classList.add("nonscroll");
            },
            blurInput(){
                this.searchFocus = false;
                document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                document.getElementsByTagName('body')[0].click();
            },
            getSearch (event) {
                this.blurInput();
                this.searchFocus = false;
                event.target.blur();
                if(this.$store.state.search.query.length > 0) {
                    if(this.$route.name !== 'search'){
                        this.$router.push('/search');
                    }
                    this.$store.dispatch('actions/initialSearchState');
                    this.$store.dispatch('actions/getCouponList', {currentSection: false, isSearch: true})
                }
            },
            resetSearch() {
                this.searchQuery = '';
                this.$refs.searchInput.focus();
            },
        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },

                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
</script>

