/**
 *    ,     
 * */

import axios from 'axios'
/**      axios
 * qs.stringify({ id: data.id }),
 */
import axiosCancel from 'axios-cancel';

axiosCancel(axios, {
    debug: false // default
});
import qs from 'qs';
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

var CancelToken = axios.CancelToken;

const state = {
    categories: new Set(),
    coupons: [],
    breadcrumbs: [],
    detail: {},
    isLoadNextPage: true,
    showSectionMenu: false,
    hidePromo: false,
    isLoad: false,
    isLoadDetail: false,
    viewType: '',
    showMap: false,
    previousSection: 0,
    coopTab: '',
    sort: {
        current: {
            order: 'new',
            by: 'desc',
            name: ''
        },
        order: {
            price: {
                code: 'price',
                name: ''
            },
            discount: {
                code: 'discount',
                name: ''
            },
            popular: {
                code: 'popular',
                name: ''
            },
            new: {
                code: 'new',
                name: ''
            },
        },
        by: {
            asc: 'asc',
            desc: 'desc',
        },
        show: false,
    },
    page: 1,
    favorites: {
        items: [],
        section: []
    },
    visited: {
        items: []
    },
    cancelToken: false,
    scrollToCoupon: false,
    couponId: '',
    scrollToCatalogList: false
}

const findCategory = function (categories, id) {
    let categoryReturn = false;
    for (let category of categories) {
        if(category.id === id) {
            categoryReturn = category;
        }
        if (!categoryReturn && category.sections.size > 0) {
            let categoryFind = findCategory(category.sections, id);
            if (categoryFind) {
                categoryReturn = categoryFind;
            }
        }
        if(categoryReturn) {
            break;
        }
    }
    return categoryReturn;
}

const buildBreadcrumbs = function (categories, id) {
    let categoryReturn = false;
    let breadcrumbs = [];
    for (let category of categories) {
        if(category.id === id) {
            categoryReturn = category;
            breadcrumbs.push(category);
        }
        if (!categoryReturn && category.sections.size > 0) {
            let categoryFind = buildBreadcrumbs(category.sections, id);
            if (categoryFind) {
                categoryReturn = categoryFind;
                breadcrumbs = [...breadcrumbs, categoryFind];
            }
        }
        if(categoryReturn) {
            break;
        }
    }
    return breadcrumbs;
}

// getters
const getters = {
    getTitle: (state, getters, rootState, rootGetters) => id => {
        let category = findCategory(state.categories, id);
        let title;
        if(!id) {
            title = '  . '+rootState.cities.current.name;
        }
        if(category) {
            title = category.name;
        }
        return title;
    },
    getTotalCoupons: state => {
        let sum = 0;
        for(let category of state.categories) {
            sum += category.count
        }
        return sum;
    },
    getCurrentSort: state => {
      return state.sort.current.name;
    },
    getCoopTab: state => {
      return state.coopTab;
    },
    getCouponsBySection: state => id => {
        let items = [];
        if(state.coupons.length > 0) {
            if(id > 0) {
                for(let coupon of state.coupons) {
                    if(coupon.id === id) {
                        items.push(coupon);
                    }
                }
            } else {
                items = state.coupons;
            }
        } else {
            items = [];
        }
        return items;
    },
    getFavorites: state => id => {
        let items = [];
        if(state.coupons.length > 0) {
            for(let coupon of state.coupons) {
                if(coupon.favorite) {
                    items.push(coupon);
                }
            }
        } else {
            items = [];
        }
        return items;
    },
}

const actions = {
    activeCategory (context, data) {
        let category = findCategory(state.categories, data.currentSection);
        if(category.count > 0 && !state.isLoad) {
            context.commit('activeCategory', {currentSection: data.currentSection});
            context.commit('hideMap');
            context.dispatch('initialCouponState');
            if(data.isLoad === true){
                context.dispatch('getCouponList', {
                    currentSection: data.currentSection,
                    reload: true
                })
            }
        } else {
            context.dispatch('initialCouponState');
        }
    },
    goCoupon (context, data) {
        //context.commit('actions/activeCategory', {id:data.id});
        //app.$router.push('/id'+data.id);
        context.commit('setViewType', 'detail');
        if(!state.detail || data.id !== state.detail.id){
            context.dispatch('initDetailPage', {route: {params: {id:data.id}}});
        }
    },
    getCategoriesList(context, data) {
        if(state.categories.size === 0 || data.reload) {
            axios.get(process.env.VUE_APP_API_PATH+'/public/categories/',{
                headers: jsonHeader,
                withCredentials: true,
                params: {
                    city: context.rootState.cities.current.xmlId,
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    if(data !== undefined && data.currentSection) {
                        response.data.result.currentSection = data.currentSection;
                    }
                    context.commit('setCategoriesList', response.data.result);
                    if(data !== undefined && data.currentSection) {
                        let breadcrumbs =  buildBreadcrumbs(state.categories, data.currentSection);
                        breadcrumbs.pop();
                        context.commit('setBreadcrumbs', breadcrumbs);
                    }
                }
            }).catch(function (error) {
            });
        }

    },
    getCouponList(context, data) {
        return new Promise((resolve, reject) => {
            if(!state.isLoad && state.isLoadNextPage || data.reload){
                axios.cancelAll();
                context.commit('setLoad', true);
                if(data.reload) {
                    context.commit('isLoadNextPage', true);
                }
                let param = {
                    sort: state.sort.current,
                    page: state.page,
                    isMobileSite: true,
                };
                context.commit('setBreadcrumbs', []);
                if(!!data.currentSection) {
                    param.section = data.currentSection;
                    let breadcrumbs =  buildBreadcrumbs(state.categories, data.currentSection);
                    breadcrumbs.pop();
                    context.commit('setBreadcrumbs', breadcrumbs);
                }

                if(data.isSearch === true) {
                    param.q = context.rootState.search.query
                }

                if(data.favorites === true) {
                    param.favorites = { ...state.favorites.items };
                    context.commit('setSectionFavorites', []);
                }

                if(data.visited === true) {
                    param.visited = { ...state.visited.items };
                    param.section = ''
                }

                if(data.favorites !== true && data.visited !== true) {
                    param.city = context.rootState.cities.current.xmlId
                }
                axios.get(process.env.VUE_APP_API_PATH+'/public/coupons/',{
                    requestId: 'getCouponList',
                    cancelToken: new CancelToken(function executor(c) {
                        context.commit('setCancelToken', c);
                    }),
                    withCredentials: true,
                    params: param,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: jsonHeader,
                }).then(response => {
                    context.commit('setLoad', false);
                    resolve();
                    if(data.reload){
                        context.commit('setCouponList', {items: []});
                    }
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        if(data !== undefined && data.currentSection) {
                            response.data.result.currentSection = data.currentSection;
                        }
                        context.commit('setCouponList', response.data.result);
                        if(response.data.result.sectionFavorites){
                            context.commit('setSectionFavorites', response.data.result.sectionFavorites);
                        }
                        if(state.favorites.items.length < 10 && data.favorites === true || state.visited.items.length < 3 && data.visited === true || response.data.result.items === null || response.data.result.items === undefined) {
                            context.commit('isLoadNextPage', false);
                        } else {
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        }
                        if(data.visited !== true) {
                            context.commit('incrementPage');
                        }
                    }
                }).catch(function (error) {
                    context.commit('setLoad', false);
                    resolve();
                });
            }
        });
    },
    getSimilars(context, data) {
        return new Promise((resolve, reject) => {
            if(!state.isLoad && state.isLoadNextPage){
                context.commit('setLoad', true);
                let param = {
                    page: state.page,
                    similar: data.id,
                    city: context.rootState.cities.current.xmlId,
                    sort: {
                        order: 'popular',
                        by: 'desc',
                    },
                }
                axios.get(process.env.VUE_APP_API_PATH + '/public/coupons_similar/', {
                    withCredentials: true,
                    params: param,
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setCouponDetailSimilar', response.data.result.similar);
                        context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                    } else {
                        if(typeof response.data !== 'object'){
                            context.commit('setErrorLoad', true);
                        }
                        else if (+response.data.error === 401) {
                            context.dispatch('auth/logout', {}, {root: true})
                        }
                    }
                    context.commit('incrementPage');
                    context.commit('setLoad', false);
                    resolve();
                }).catch(function (error) {
                    resolve();
                });
            } else {
                resolve();
            }
        });
    },
    getViewType(context, data) {
        let id = data.id;
        let category = findCategory(state.categories, id);
        let result;
        if(id > 0) {
            if(category) {
                result = 'list';
            } else {
                let item = state.coupons.filter(coupon => +coupon.id === +id);
                if(item.length > 0) {
                    result = 'detail';
                } else {
                    result = 'notFound';
                }
            }
        } else {
            result = 'list';
        }
        context.commit('setViewType', result);
    },
    setFavorite(context, data) {
        let favorites = VueCookies.get('favorites');
        if(favorites === null){
            favorites = {
                items: []
            };
        }
        let index = favorites.items.indexOf(data.id);
        if(index !== -1) {
            favorites.items.splice(index, 1);
        } else {
            favorites.items.push(data.id);
        }
        VueCookies.set('favorites', favorites);
        if(data.isDetail){
            context.commit('updateCouponDetail', data);
        } else if(data.isSimilar){
            context.commit('updateCouponSimilar', data);
        } else {
            context.commit('updateCoupon', data);
        }
        context.commit('setFavorites', favorites.items);
    },
    setFavoritesCookies(context) {
        let favorites = VueCookies.get('favorites');
        if(favorites !== null) {
            context.commit('setFavorites', favorites.items);
        }
    },
    setVisited(context, data) {
        let visited = VueCookies.get('visited');
        if(visited === null){
            visited = {
                items: []
            };
        }
        let index = visited.items.indexOf(data.id);
        if(index === -1) {
            visited.items.push(data.id);
            VueCookies.set('visited', visited);
            context.commit('setVisited', visited.items);
        }
    },
    setVisitedCookies(context) {
        let visited = VueCookies.get('visited');
        if(visited !== null) {
            context.commit('setVisited', visited.items);
        }
    },
    initialCouponState(context) {
        context.commit('isLoadNextPage', true);
        context.commit('setPage', 1);
        context.commit('setCouponList', {items: []});
        /*if(typeof state.cancelToken  === 'function') {
            state.cancelToken();
            context.commit('setCancelToken', false);
        }*/
        context.commit('setViewType', 'list');
        context.commit('hideMap');
    },
    initialSearchState(context) {
        context.commit('isLoadNextPage', true);
        context.commit('setPage', 1);
        context.commit('setCouponList', {items: []});
        context.commit('setViewType', 'list');
        context.commit('hideMap');

    },
    initialCatalogState(context, data) {
        context.commit('isLoadNextPage', true);
        context.commit('setPage', 1);
        context.commit('hideMap');
        context.commit('setCouponList', {items: []});
        if(typeof state.cancelToken  === 'function') {
            state.cancelToken();
            context.commit('setCancelToken', false);
        }

        /*if(data.route.path === '/') {
            context.commit('setSort', {order: state.sort.order.new.code, by: state.sort.by.desc});
        } else {
            context.commit('setSort', {order: state.sort.order.price.code, by: state.sort.by.desc});
        }*/
    },
    initDetailPage(context, data) {
        return new Promise((resolve, reject) => {
            context.commit('opinions/clearData', {}, {root: true});
            context.commit('setCouponDetail', {});
            context.commit('isLoadNextPage', true);
            context.commit('setPage', 1);
            if (!state.isLoadDetail && data.route.params.id !== undefined) {
                context.commit('setLoadDetail', true);
                axios.get(process.env.VUE_APP_API_PATH + '/public/coupons/' + data.route.params.id, {
                    withCredentials: true,
                    params: {
                        pageSize: 3,
                        city: context.rootState.cities.current.xmlId,
                        sort: {
                            order: 'popular',
                            by: 'desc',
                        },
                    },
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    context.commit('setLoadDetail', false);
                    if (response.data.result.item === false || response.data.result.viewType === 'notFound') {

                    } else if (typeof response.data === 'object' && +response.data.error === 0) {
                        let favorites = VueCookies.get('favorites');
                        if (favorites === null) {
                            favorites = {
                                items: []
                            };
                        }
                        let index = favorites.items.indexOf(data.route.params.id);
                        if (index !== -1) {
                            response.data.result.item.favorite = true
                        } else {
                            response.data.result.item.favorite = false
                        }
                        for (let similarIndex in response.data.result.item.similars) {
                            let index = favorites.items.indexOf(response.data.result.item.similars[similarIndex].id);
                            if (index !== -1) {
                                response.data.result.item.similars[similarIndex].favorite = true
                            } else {
                                response.data.result.item.similars[similarIndex].favorite = false
                            }
                        }
                        context.commit('setCouponDetail', response.data.result);
                        context.dispatch('setVisited', response.data.result.item);
                    } else {
                        if (+response.data.error === 401) {
                            context.dispatch('auth/logout', {}, {root: true})
                        }
                    }
                    resolve();
                }).catch(function (error) {
                    context.commit('setLoadDetail', false);
                    resolve();
                });
            } else {
                resolve();
            }
        });
    },

    getMarkersByAddresses(context, data) {
        return new Promise((resolve, reject) => {
            for(let index in data) {
                setTimeout(function () {
                    axios.get(process.env.VUE_APP_API_PATH+'/'+data.type+'/menus/',{
                        headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                        params: {
                            type: 'action',
                        },
                        paramsSerializer: function(params) {
                            return qs.stringify(params, {arrayFormat: 'repeat'})
                        },
                        withCredentials: true,

                    }).then(response => {
                        if(typeof response.data === 'object' && +response.data.error === 0) {
                            context.commit('setActionMenu', response.data.result);
                            resolve();
                        } else {
                            if( +response.data.error===401){
                                context.dispatch('auth/logout',{},{root:true})
                            }
                        }
                    }).catch(function (error) {
                        reject();
                    });
                }, 2000);
            }
        });
    },
}

let setShowCategoriesList = function (categories, currentSection) {
    let items = new Set();
    for(let category of categories) {
        let item = category;
        item.show = +item.id === +currentSection;
        if(category.sections.size > 0 || item.sections.length > 0) {
            Vue.set(item, 'sections',setShowCategoriesList(item.sections, currentSection));
            for(let child of item.sections) {
                if(child.show === true ) {
                    item.show = true;
                }
            }
        }
        items.add(item);
    }
    return items;
}

let toggleSection = function (categories, currentSection) {
    let items = new Set();
    for(let category of categories) {
        let item = category;
        if(+category.id === +currentSection) {
            item.show = !category.show
        } else {
            if(category.sections.size > 0 || item.sections.length > 0) {
                category.sections = toggleSection(category.sections, currentSection);
            }
        }
        items.add(item);
    }
    return items;
}

const mutations = {
    setCategoriesList(state, data) {
        Vue.set(state, 'categories',setShowCategoriesList(data.items, data.currentSection));
    },
    setCoopTab(state, data) {
        state.coopTab = data;
    },
    setCouponList(state, data) {
        if(data !== null && data.items !== null) {
            if(state.page < 2) {
                state.coupons = [];
            }
            for (let itemIndex in data.items) {
                if(data.items.hasOwnProperty(itemIndex)) {
                    let item = data.items[itemIndex];
                    if(state.favorites.items.indexOf(item.id) !== -1) {
                        item.favorite = true;
                    }
                    let itemFind = state.coupons.find(coupon => +coupon.id === +item.id);
                    if(!itemFind){
                        state.coupons.push(item);
                    }
                }
            }
        }
    },
    setCouponDetailSimilar(state, data) {
        if(data !== null && data.items !== null) {
            if(state.page < 2) {
                state.detail.similars = [];
            }
            for (let itemIndex in data.items) {
                if(data.items.hasOwnProperty(itemIndex)) {
                    let item = data.items[itemIndex];
                    if(state.favorites.items.indexOf(item.id) !== -1) {
                        item.favorite = true;
                    }
                    let itemFind = state.detail.similars.find(coupon => +coupon.id === +item.id);
                    if(!itemFind){
                        state.detail.similars.push(item);
                    }
                }
            }
        }
    },
    updateCouponDetail(state, data) {
        Vue.set(state.detail, 'favorite', !data.favorite);
    },
    updateCoupon(state, data) {
        for (let itemIndex in state.coupons) {
            if(data.id === state.coupons[itemIndex].id) {
                data.favorite = !data.favorite;
                Vue.set(state.coupons, itemIndex, data);
            }
        }
    },
    updateCouponSimilar(state, data) {
        for (let itemIndex in state.detail.similars) {
            if(data.id === state.detail.similars[itemIndex].id) {
                data.favorite = !data.favorite;
                Vue.set(state.detail.similars, itemIndex, data);
            }
        }
    },
    setCouponDetail(state, data) {
        Vue.set(state, 'detail', data.item);
    },
    incrementCouponDetailCountOpinion(state) {
        Vue.set(state.detail, 'opinionCount', state.detail.opinionCount +1);
    },
    activeCategory(state, data) {
        state.categories = setShowCategoriesList(state.categories, data.currentSection);
    },
    toggleSection(state, data) {
        state.categories = toggleSection(state.categories, data.currentSection);
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setViewType(state, data) {
        state.viewType = data;
    },
    toggleMap(state) {
        state.showMap = !state.showMap;
    },
    hideMap(state) {
        state.showMap = false;
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setLoadDetail(state, data) {
        state.isLoadDetail = data;
    },
    setSort(state, data) {
        state.sort.current = data;
    },
    setShowSort(state, data) {
        state.sort.show = data;
    },
    setPage(state, data) {
        state.page = data;
    },
    incrementPage(state) {
        state.page++;
    },
    setFavorites(state, data) {
        state.favorites.items = data;
    },
    setSectionFavorites(state, data) {
        state.favorites.section = data;
    },
    setVisited(state, data) {
        state.visited.items = data;
    },
    previousSection(state, data) {
        state.previousSection = data;
    },
    setCancelToken(state, data) {
        state.cancelToken = data;
    },
    setCouponToScroll(state,data){
      state.couponId = data;
      state.scrollToCoupon = true;
    },
    cancelCouponToScroll(state){
      state.scrollToCoupon = false;
      state.couponId = '';
    },
    scrollToCatalogList(state,data){
      state.scrollToCatalogList = data;
    },
    sectionMenuOpen(state, data) {
        state.showSectionMenu = true;
        document.getElementsByTagName('body')[0].classList.add('nonscroll');
    },
    sectionMenuClose(state, data) {
        state.showSectionMenu = false;
        document.getElementsByTagName('body')[0].classList.remove('nonscroll');
    },
    setBreadcrumbs(state, data) {
        state.breadcrumbs = data;
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
