<template>
    <div>
        <div>
            <textarea-field  placeholder="  "
                             v-model="messageText"
                             :auto-focus="true"
                             :is-error="message_error"
                             :add-style="{height: 200 + 'px'}"
                             @focus="removeError()"
            ></textarea-field>
        </div>


        <div class="row-b mt32 button-block">
            <button class="btn" @click="sendMess()"></button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'

    export default {
        name: "MailZems",
        components: {
            TextareaField,
        },
        props: {
            defaultText: {
                default() {
                    return '';
                }
            }
        },
        data: function() {
            return {
                messageText: '',
                message_error: false,
                send_message_success: true
            }
        },
        created() {},
        mounted() {
            this.messageText = this.defaultText;
        },
        methods: {
            sendMess: function() {
                let  self = this;
                self.message_error = false;
                if (self.messageText === '') {
                    setTimeout(function () {
                        self.message_error = true;
                    }, 300);
                } else {
                    self.message_error = false;
                    this.$store.dispatch('mail/sendMessage', {message: self.messageText});
                    self.newMessModalOpen();
                }
            },
            newMessModalOpen: function () {
                this.send_message_success = false;
                this.$store.commit('popups/open', {name: this.popups.mailSent.name});
                this.$store.commit('popups/close', {name: this.popups.mailZems.name});
            },
            newMessModalClose: function () {
                this.send_message_success = false;
                this.$store.commit('popups/close', {name: this.popups.mailSent.name});

            },
            removeError () {
                this.message_error = false;
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

