<template>
        <div class="main-nav-block" v-touch:swipe="swipeHandler">
            <div class="main-nav" :class="{slide:showTopMenu}">
                <div class="popup-header dark">
                    <div class="popup-name" @click="showPopup('citySelect')">
                        <svg class="icon-map-track">
                          <use xlink:href="@/assets/img/mob-sprite.svg#map-track2"></use>
                        </svg>
                        {{currentCity.name}}
                        <svg class="edit-icon">
                          <use xlink:href="@/assets/img/mob-sprite.svg#pencil"></use>
                        </svg>
                    </div>
                    <div @click="topMenuClose" class="close" >
                        <span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg>
                      </span>
                    </div>
                </div>
                <div class="h-modal-inner" ref="topmenu">
                    <div class="nav-items">
                        <!--<router-link @click.native="topMenuClose" v-if="user.firstName" class="nav-item" to="/personal/profile">
                            <div class="left-icon"><span class="i icon-user">
                                <svg class="icon">
                                    <use xlink:href="@/assets/img/mob-sprite.svg#user"></use>
                                </svg></span>
                            </div>
                                <div class="ni-right">
                                    <div class="nav-txt">{{ user.firstName }} {{ user.lastName[0] }}.</div>
                                    <div class="chevron"><span class="i icon-chevron">
                                        <svg class="icon">
                                          <use xlink:href="@/assets/img/mob-sprite.svg#chevron"></use>
                                        </svg></span>
                                    </div>
                            </div>
                        </router-link>
                        <a v-else class="nav-item" href="javascript:void(0);" @click="openAuthorization(false)">
                            <div class="left-icon"><span class="i icon-user">
                                <svg class="icon">
                                    <use xlink:href="@/assets/img/mob-sprite.svg#user"></use>
                                </svg></span>
                            </div>
                            <div class="ni-right">
                                <div class="nav-txt"> / </div>
                                <div class="chevron"><span class="i icon-chevron">
                                        <svg class="icon">
                                          <use xlink:href="@/assets/img/mob-sprite.svg#chevron"></use>
                                        </svg></span>
                                </div>
                            </div>
                        </a>-->
                        <router-link v-if="token" @click.native="topMenuClose" class="nav-item" to="/personal">
                            <div class="left-icon">
                                <svg class="i" style="height: 16px; width: 16px;">
                                    <use xlink:href="@/assets/img/mob-sprite.svg#ticket2"></use>
                                </svg>
                            </div>
                            <div class="ni-right">
                                <div class="nav-txt"> </div>
                                <div class="chevron"><span class="i icon-chevron">
                                    <svg class="icon">
                                      <use xlink:href="@/assets/img/mob-sprite.svg#chevron"></use>
                                    </svg></span></div>
                            </div>
                        </router-link>
                        <a v-else class="nav-item" href="javascript:void(0);" @click="openAuthorization(true)">
                            <div class="left-icon">
                              <svg class="i icon-ticket">
                                <use xlink:href="@/assets/img/mob-sprite.svg#ticket2"></use>
                            </svg>
                            </div>
                            <div class="ni-right">
                                <div class="nav-txt"> </div>
                                <div class="chevron"><span class="i icon-chevron">
                                        <svg class="icon">
                                          <use xlink:href="@/assets/img/mob-sprite.svg#chevron"></use>
                                        </svg></span>
                                </div>
                            </div>
                        </a>
                        <router-link v-for="item in menuList"
                                     class="nav-item ni-about"
                                     :key="item.link"
                                     v-if="!item.hideMobile"
                                     @click.native="topMenuClose"
                                     :to="item.link">
                            <div class="left-icon">
                                <svg class="icon-info">
                                    <use v-if="item.iconMobile === 'info' || item.link === '/about/'" xlink:href="@/assets/img/mob-sprite.svg#info"></use>
                                    <use v-if="item.iconMobile === 'beaker' || item.link === '/rating/'" xlink:href="@/assets/img/mob-sprite.svg#beaker"></use>
                                    <use v-if="item.iconMobile === 'stats' || item.link === '/statistic/'" xlink:href="@/assets/img/mob-sprite.svg#stats"></use>
                                    <use v-if="item.iconMobile === 'coop' || item.link === '/cooperation/'" xlink:href="@/assets/img/mob-sprite.svg#coop"></use>
                                    <use v-if="item.iconMobile === 'faq' || item.link === '/faq/'" xlink:href="@/assets/img/mob-sprite.svg#faq"></use>
                                    <use v-if="item.link === '/deposit/'" xlink:href="@/assets/img/mob-sprite.svg#deposit"></use>
                                    <use v-if="item.iconMobile === 'contact-info' || item.link === '/contacts/'" xlink:href="@/assets/img/mob-sprite.svg#contact-info"></use>
                                </svg>
                            </div>
                            <div class="ni-right">
                                <div class="nav-txt">{{item.name}}</div>
                                <div class="chevron"><span class="i icon-chevron">
                                    <svg class="icon">
                                      <use xlink:href="@/assets/img/mob-sprite.svg#chevron"></use>
                                    </svg></span>
                                </div>
                            </div>
                        </router-link>
                    </div>
                    <a href="https://zemskidki.ru/?full_version" class="link mt40">  </a>
                    <a class="link" href="javascript:void(0);" @click="openPublicOffer"> </a>
                    <div class="nav-copyright"> 1980-2092  </div>
                </div>
            </div>
        </div>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: "TopMenu",
        props: {
            type: {
                default() {
                    return 'public'
                }
            }
        },
        created () {
            if(this.type !== 'public') {
                this.$store.dispatch('menus/getMenu',{type:this.type}).then((response)=>{
                    this.$store.commit('menus/setMenu', response);
                });
            }
        },
        data(){
            return {
                menuItems: [
                    {
                        name: ' ',
                        link:'/about/'
                    },
                    /*{
                        name: '',
                        link:'/rating/'
                    },*/
                    {
                        name: '',
                        link:'/deposit/'
                    },
                    {
                        name: '',
                        link:'/statistic/'
                    },
                    {
                        name: '',
                        link:'/cooperation/'
                    },
                    {
                        name: '  ',
                        link:'/faq/'
                    },
                    {
                        name: ' ',
                        link:'/contacts/'
                    },

                ]
            }
        },
        computed: {
            ...mapState({
                user: state => state.users.user,
                showTopMenu: state => state.menus.showTopMenu,
                popups: state => state.popups.items,
                currentCity:  state => state.cities.current,
            }),
            menuList(){
                return this.type === 'public' ? this.menuItems : this.$store.state.menus.itemsTop;
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
        },
        methods:{
            openPublicOffer() {
                this.$store.commit('popups/open', {name: this.popups.publicOffer.name});
            },
            swipeHandler(direction){
              if(direction == 'right'){
                this.$store.commit('menus/topMenuClose');
              }
            },
            topMenuOpen(){
                this.$store.commit('menus/topMenuOpen');
            },
            topMenuClose(){
                this.$store.commit('menus/topMenuClose');
                this.$refs.topmenu.scrollTop = 0;
            },
            openAuthorization: function (setIsRedirect) {
                this.$store.commit('menus/topMenuClose');
                this.$store.commit('auth/setIsRedirect', setIsRedirect);
                this.$store.commit('popups/open', {name: this.popups.publicAuth.name});
            },
            showPopup(popup) {
                this.$store.commit('menus/topMenuClose');
                this.$store.commit('popups/open',{name:popup});
            },
        }
    }
</script>

