<template>
    <div class="popup-block" :class="[{open:show},addClass]">
        <transition name="fade-popup">
            <div class="popup" v-if="show">
                <div class="popup-header" v-if="!isSetBlank">
                    <div class="popup-name">{{title}}</div>
                    <div class="close" v-if="isBtnClose" @click="close"><span class="i icon-close">
                    <svg class="icon">
                      <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                    </svg></span>
                    </div>
                </div>
                <div :class="{'popup-content':!isSetBlank}"
                     ref="overlayPopup">
                    <div :class="{'inner':!isSetBlank}">
                        <slot></slot>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Popup",
        props: {
            width: {
                default: function () {
                    return 320
                }
            },
            addClass: {default: ''},
            show: {
                default: function () {
                    return false
                }
            },
            isSetTop: {
                default: function () {
                    return false
                }
            },
            isBtnClose: {
                default: function () {
                    return true
                }
            },
            isSetStyled: {
                default: function () {
                    return false
                }
            },
            isSetBlank: {
                default: function () {
                    return false
                }
            },
            isResize: {
                default: function () {
                    return false
                }
            },
            name: '',
            title: {
                default: ''
            },
        },
        watch: {
            show: function () {
                if (this.show) {
                    if (this.isSetTop) {
                        this.updateViewportSize();
                    }
                    document.getElementsByTagName('body')[0].classList.add("nonscroll");
                }
            },
            isResize: function (newV, oldQV) {
                if (this.isSetTop && (newV !== oldQV)) {
                    this.updateViewportSize();
                }
            },
        },
        data: function () {
            return {
                top: 20,
            }
        },
        mounted: function () {
            if (this.isSetTop) {
                this.topListener();
            }
        },
        methods: {
            updateViewportSize: function () {
                this.$nextTick(function () {
                    if (typeof(this.$refs.overlayPopup) !== 'undefined') {
                        let top = this.getViewportSize().h - this.$refs.overlayPopup.clientHeight;
                        if (top < 1) {
                            top = 1
                        }
                        this.top = (top / 2);
                    }
                });
            },
            updatePopupStyled: function () {
                this.$nextTick(function () {
                    this.$refs.overlayPopup.classList.add('popup--styled')
                })
            },
            topListener: function () {
                let self = this;
                this.$nextTick(function () {
                    self.updateViewportSize();
                    window.addEventListener('resize', self.updateViewportSize);
                });

            },

            getViewportSize: function () {
                let d = window.document.querySelector("html");
                return {w: d.clientWidth, h: d.clientHeight};
            },

            close: function () {
                if(this.show && this.name){
                    this.$store.commit('popups/close', {name: this.name});
                    this.$emit('close');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                } else {
                    this.$emit('close');
                    document.getElementsByTagName('body')[0].classList.remove("nonscroll");
                }
                this.$refs.overlayPopup.scrollTop = 0;
            }
        }
    }
</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: transform .3s ease;
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);
    }
    .fade-popup-enter, .fade-popup-leave-to {
        transform: translateX(320px);
    }
    .popup{
        left: 0;
        top: 0;
        z-index: 8;
        position: relative;
    }
    .popup-block{
        width: 320px;
        overflow: hidden;
        position: fixed;
        z-index: 9;
        left: 0;
        top: 0;
    }
    .popup-block.open .popup{
        min-height: 100vh;
    }
    .popup-block.open{
        min-height: 100vh;
    }
    .popup-block.popup--gallery{
      z-index: 1000 !important;
    }
</style>
