export function validateBik(bik) {
    var result = false;
    let error = {};
    if (typeof bik === 'number') {
        bik = bik.toString();
    } else if (typeof bik !== 'string') {
        bik = '';
    }
    if (!bik.length) {
        error.code = 1;
        error.message = '    9 ';
    } else if (/[^0-9]/.test(bik)) {
        error.code = 2;
        error.message = '    9 ';
    } else if (bik.length !== 9) {
        error.code = 3;
        error.message = '    9 ';
    } else {
        result = true;
    }
    return result?result:error;
}

export function validateInn(inn, isIP) {
    var result = false;
    let error = {};
    if (typeof inn === 'number') {
        inn = inn.toString();
    } else if (typeof inn !== 'string') {
        inn = '';
    }
    if (!inn.length) {
        error.code = 1;
        error.message = '      10- ';
        if(isIP === true) {
            error.message = '     12- ';
        }
    } else if (/[^0-9]/.test(inn)) {
        error.code = 2;
        error.message = '      10- ';
        if(isIP === true) {
            error.message = '     12- ';
        }
    } else if (inn.length < 12 && isIP || inn.length < 10 && !isIP) {
        error.code = 3;
        error.message = '      10- ';
        if(isIP === true) {
            error.message = '     12- ';
        }

    } else {
        var checkDigit = function (inn, coefficients) {
            var n = 0;
            for (var i in coefficients) {
                n += coefficients[i] * inn[i];
            }
            return parseInt(n % 11 % 10);
        };
        switch (inn.length) {
            case 10:
                var n10 = checkDigit(inn, [2, 4, 10, 3, 5, 9, 4, 6, 8]);
                if (n10 === parseInt(inn[9])) {
                    result = true;
                }
                break;
            case 12:
                var n11 = checkDigit(inn, [7, 2, 4, 10, 3, 5, 9, 4, 6, 8]);
                var n12 = checkDigit(inn, [3, 7, 2, 4, 10, 3, 5, 9, 4, 6, 8]);
                if ((n11 === parseInt(inn[10])) && (n12 === parseInt(inn[11]))) {
                    result = true;
                }
                break;
        }
        if (!result) {
            error.code = 4;
            error.message = ' ';
        }
    }
    return result?result:error;
}

export function validateKpp(kpp) {
    var result = false;
    let error = {};
    if (typeof kpp === 'number') {
        kpp = kpp.toString();
    } else if (typeof kpp !== 'string') {
        kpp = '';
    }
    if (!kpp.length) {
        error.code = 1;
        error.message = '    9 ';
    } else if (kpp.length !== 9) {
        error.code = 2;
        error.message = '    9 ';
    } else if (!/^[0-9]{4}[0-9A-Z]{2}[0-9]{3}$/.test(kpp)) {
        error.code = 3;
        error.message = ' ';
    } else {
        result = true;
    }
    return result?result:error;
}

export function validateKs(ks, bik) {
    var result = false;
    let error = {};
    if (typeof ks === 'number') {
        ks = ks.toString();
    } else if (typeof ks !== 'string') {
        ks = '';
    }
    if (!ks.length) {
        error.code = 1;
        error.message = '/    20 ';
    } else if (/[^0-9]/.test(ks)) {
        error.code = 2;
        error.message = '/    20 ';
    } else if (ks.length !== 20) {
        error.code = 3;
        error.message = '/    20 ';
    } else {
        var bikKs = '0' + bik.toString().slice(4, 6) + ks;
        var checksum = 0;
        var coefficients = [7, 1, 3, 7, 1, 3, 7, 1, 3, 7, 1, 3, 7, 1, 3, 7, 1, 3, 7, 1, 3, 7, 1];
        for (var i in coefficients) {
            checksum += coefficients[i] * (bikKs[i] % 10);
        }
        if (checksum % 10 === 0) {
            result = true;
        } else {
            error.code = 4;
            error.message = ' /';
        }
    }
    return result?result:error;
}

export function validateOgrn(ogrn) {
    var result = false;
    let error = {};
    if (typeof ogrn === 'number') {
        ogrn = ogrn.toString();
    } else if (typeof ogrn !== 'string') {
        ogrn = '';
    }
    if (!ogrn.length) {
        error.code = 1;
        error.message = '    13 ';
    } else if (/[^0-9]/.test(ogrn)) {
        error.code = 2;
        error.message = '    13 ';
    } else if (ogrn.length !== 13) {
        error.code = 3;
        error.message = '    13 ';
    } else {
        var n13 = parseInt((parseInt(ogrn.slice(0, -1)) % 11).toString().slice(-1));
        if (n13 === parseInt(ogrn[12])) {
            result = true;
        } else {
            error.code = 4;
            error.message = ' ';
        }
    }
    return result?result:error;
}

