<template>
    <div class="w-tooltip" 
         :class="addClassWrap"
         ref="tooltipWrap"
         v-clickoutside="tooltipClose">
        <span ref="tooltipBtn" class="tooltip__btn" @click.prevent="btnClick" :class="addClassBtn">
            <slot name="tooltipBtn"></slot>
        </span>
        
        <transition name="fade-popup">
            <div class="tooltip" 
                 ref="tooltip" 
                 v-if="show" 
                 :class="[addClass, direction]"
                 :style="addStyle">
                <div v-if="isBtnClose" class="tooltip__close" @click.prevent="btnClick"></div>
                <div class="tooltip__content" :style="{'padding':!isPadding ? '0': ''}">
                    <slot name="tooltipContent"></slot>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "Tooltip",
        data: function() {
            return {
                show: false,
            }
        },
        props: {
            direction: {default: 'bottom-center-direction'},
            addStyle: {default: ''},
            addClass: {default: ''},
            addClassWrap: {default: ''},
            addClassBtn: {default: ''},
            tempID: {default: ''},
            isBtnClose: {
                default: function () {
                    return true
                }
            },
            isPadding: {
                default: function () {
                    return true
                }
            }
        },
        methods: {
            btnClick: function() {
                if(this.show) {
                    this.tooltipClose();
                } else {
                    this.tooltipShow();
                }
            },
            tooltipClose: function() {
                let elem = this;
                this.show = false;
                
                setTimeout(() => {
                    this.$emit('tooltipclose', this.tempID);
                }, 100);
            },
            tooltipShow: function() {
                this.show = true;
                this.$emit('tooltipshow', this.tempID);
            },
            
        },
        directives: {
            'clickoutside': {
                bind: function(el, binding, vNode) {
                    if (typeof binding.value !== 'function') {
                        const compName = vNode.context.name;
                        let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                        if (compName) { warn += `Found in component '${compName}'` }
                        //console.warn(warn);
                    }
                    const bubble = binding.modifiers.bubble;
                    const handler = (e) => {
                        if (bubble || (!el.contains(e.target) && el !== e.target) && (!e.target.classList.contains('click-outside-tooltip'))) {
                            binding.value(e)
                        }
                    }
                    el.__vueClickOutside__ = handler;
                    document.addEventListener('click', handler);
                },
              
                unbind: function(el, binding) {
                    document.removeEventListener('click', el.__vueClickOutside__);
                    el.__vueClickOutside__ = null;
                }
            }
        }
    }
    
    
</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: opacity .15s;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
    }
</style>