import axios from 'axios'
import {jsonHeader} from '@/helpers/axiosHeaders'
import qs from "qs";
const state = {
    isSend: false
}

// getters
const getters = {

}

const actions = {
    sendForm(context, data) {
        return new Promise((resolve, reject) => {
            if(!state.isSend){
                context.commit('setSend', true);

                let formData = new FormData();

                for(let keyProp in data.params) {
                    formData.append(keyProp, data.params[keyProp]);
                }

                if(data.params.formName === 'cooperation'){
                    formData.append('isMobileSite', true);
                }

                axios.post(process.env.VUE_APP_API_PATH+'/public/forms/',formData,{
                    withCredentials: true,
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                }).then(response => {
                    context.commit('setSend', false);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        if(data.params.formName === 'cooperation' && response.data.result.status === 'ok') {
                            context.commit('popups/open', {name: context.rootState.popups.items.cooperationAccepted.name},{root:true});
                        }
                    } else {

                        if(data.params.formName === 'cooperation' && +response.data.error === 401) {
                            context.commit('popups/open', {name: context.rootState.popups.items.cooperationErrorPhone.name},{root:true});
                        } else {
                            if( +response.data.error===401){
                                context.dispatch('auth/logout',{},{root:true})
                            }
                        }
                    }
                    resolve();
                }).catch(function (error) {
                    context.commit('setSend', false);
                    resolve();
                });
            }
        });
    },
}

const mutations = {
    setSend(state, data) {
        state.isSend = data;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}