<template>
    <div class="inp-s-wrap full" :class="{ '--icon': isEdit || isValidate || isClearBtn, active: showLabel, 'inp-s-disabled': disabled, 'error': isError, 'repeat-error':  repeatError }">
        <span class="inp-s-label" :class="{ error: isError, 'repeat-error':  repeatError }">{{ placeholder }}</span>

        <input :type="type"
            ref="field"
            :id="inputId"
            @keyup.enter="enter($event)"
            @focus="setFocus(true)"
            @blur="setFocus(false)"
            @keyup.ctrl.90="clearName"
            class="inp-s"
            :placeholder="showPlaceholder ? placeholderInput : ''"
            :class="[{ error: isError, disabled:disabled,ok: isValidate, 'repeat-error':  repeatError}, classBtn]"
            :value="value"
            v-mask="mask"
            :name="nameInput"
            @input="updateValue($event.target.value)"
            :maxlength="maxLengthNum"
            :autocomplete="fieldAutocomplete"
            :pattern="(!pattern)?'.*':'pattern'"
        >
        <span class="inp-s-icon">
            <svg class="--edit" v-if="isEdit" @click="edit">
              <use xlink:href="@/assets/img/mob-sprite.svg#edit"></use>
            </svg>
            <svg class="--error" v-if="isError">
              <use xlink:href="@/assets/img/mob-sprite.svg#error"></use>
            </svg>
            <svg class="--ok" v-if="isValidate" @click="check">
              <use xlink:href="@/assets/img/mob-sprite.svg#check"></use>
            </svg>
        </span>
        <span class="inp-s-error-text" v-if="isError">{{ errorText }}</span>
        <span class="field-clear__btn" v-if="isClearBtn && currentModel.length > 0" @click="clear" >
            <svg class="icon-c-remove">
                <use xlink:href="@/assets/img/mob-sprite.svg#c-remove"></use>
            </svg>
        </span>
        <svg class="icon-h-search" v-if="!showLabel">
            <use xlink:href="@/assets/img/mob-sprite.svg#h-search"></use>
        </svg>
        <svg class="icon-h-search-active" v-if="showLabel">
            <use xlink:href="@/assets/img/mob-sprite.svg#h-search"></use>
        </svg>
        <span class="inp-s-counter" v-if="maxLength">{{ maxLengthNum - value.length }}</span>
        <!--<svg class="icon-h-search">
            <use xlink:href="@/assets/img/mob-sprite.svg#h-search"></use>
          </svg>-->

    </div>
</template>

<script>
    import {number_format} from '@/helpers/formatted.js';

    export default {
        name: "InputStyled",
        props: {
            value: {default: ''},
            placeholder: {default: ''},
            placeholderInput: {default: ''},
            pattern: {default: false},
            inputId: {default: ''},
            disabled: {default: false},
            classBtn: {default: ''},
            isError: {default: false},
            isEdit: {default: false},
            isBlockArrowPhone: {default: false},
            isMoney: {default: false},
            maxMoney: {default: 9999999},
            isValidate: {default: false},
            errorText: {default: ''},
            currentModel: {default: ''},
            mask: {default: ''},
            name: {default: 'name'},
            isClearBtn: {default: false},
            maxLength: {default: false},
            maxLengthNum: {default: ''},
            fieldAutocomplete: {default: 'nope'},
            type: {default: 'text'},
        },
        data: function () {
            return {
                focus: false,
                showPlaceholder: false,
                nameInput: '',
                repeatError: false,
            }
        },
        mounted(){
            if(this.name !== 'name'){
                this.nameInput = this.name;
            } else {
                this.nameInput = Math.random().toString(36).substr(2, 5);
            }
            if(this.isBlockArrowPhone) {
                this.$refs.field.addEventListener("keydown", this.blockArrow, false);
            }
        },
        beforeDestroy() {
            this.$refs.field.removeEventListener('keydown', this.blockArrow);
        },
        computed: {
            showLabel() {
                return this.focus || this.value.toString().length > 0;
            },
        },
        watch:{
            isError(val,oldVal){
                if(oldVal){
                    this.repeatError = true;
                }
                if(this.isError){
                    if(this.$el.closest('.popup-wrapper')){
                        let $top = 0;
                        let pr = this.$el.closest('.popup-wrapper');
                        $top += parseFloat(pr.offsetTop);
                        pr.scrollTo({top: $top, left: 0, behavior: 'smooth'});
                    }
                }
            }
        },
        methods: {
            clearName(){
                if(this.name === 'nameCompany'){
                    let val = this.$refs.field.value.replace(//g,'').replace(//g,'');
                    this.$refs.field.value = val
                    this.$emit('input', val);
                }
            },
            blockArrow(e){
                if ((e.keyCode >= 37 && e.keyCode <= 40)) {
                    e.preventDefault();
                    return false;
                }
            },
            updateValue: function (value) {
                let v = value;
                if(this.isMoney) {
                    v = v.replace(/[-;":'a-zA-Z--\\=`/\*++!@#$%\^&_?><\s]/g, "");
                    if(+v > this.maxMoney) {
                        v = this.maxMoney;
                        v = number_format(v, 0, '', ' ')

                    } else {
                        if(v !== ''){
                            v = number_format(v, 0, '', ' ')
                        }

                    }
                    this.$refs.field.value = v
                }
                if(this.mask && this.mask.mask == '9{1,2}%'){
                    if(+v !== 0){
                        v = +v;
                        this.$refs.field.value = v
                    }
                }
                this.$emit('input', v);
            },
            enter: function (event) {
                this.$emit('enter',event);
            },
            check: function () {
                this.$emit('check')
            },
            clear: function() {
                this.$refs.field.focus();
                this.$emit('input', '');
            },
            setFocus(value) {
                let self = this
                value = value || false;
                this.focus = value;
                this.repeatError = false;
                if(value){
                    this.$emit('focus');
                    setTimeout(function () {
                        self.showPlaceholder = true;
                    }, 50);
                } else {
                    this.$emit('blur');
                    this.showPlaceholder = false;
                }
            },
            edit: function () {
                this.$emit('edit');
            },

        }
    }
</script>

<style scoped>
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    input[type='number'] {
        -moz-appearance: textfield;
    }
</style>
