<template>
    <div>
        <div class="popup-city__field field-clear">
            <input-styled v-model="citySearch"
                          name="citySearch"
                        :remove-btn="true"
                        :current-model="citySearch"
                        :placeholder="'  '"
                        :placeholder-input="'  '"
                        :is-clear-btn="true"></input-styled>
        </div>

        <div class="cities-list" v-if="init">
            <a @click="setCity(city)"
               href="javascript:void(0);"
               class="city-item"
               :class="{active:city.id === current.id}"
               v-if="!(!isShowAll && +city.count === 0)"
               v-for="city of citiesSearch"
               :key="city.id">
                <span class="city-name" :style="{'font-weight': ((city.name.toLowerCase() === '' || city.name.toLowerCase() === '-') && city.id !== current.id)?'bold':''}">
                    {{ city.name }}
                    <svg class="icon-check">
                      <use xlink:href="@/assets/img/mob-sprite.svg#check"></use>
                    </svg>
                </span>
                <span class="count">{{ city.count }}</span>
            </a>
              <div class="not-found" v-if="!citiesSearch.length">
                <svg>
                  <use xlink:href="@/assets/img/mob-sprite.svg#ufo"></use>
                </svg>
                <div class="txt">    ,<br>     </div>
            </div>
        </div>
    </div>
</template>


<script>
    import { mapState } from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'

    export default {
        name: "CitySelect",
        components: {
            InputStyled
        },
        props: {
            isShowAll: {
               default() {
                   return true;
               }
            },
            isSet: { //  true,    ,     @set.city
                default() {
                    return true;
                }
            },
            textNote: {
                default() {
                    return '(      ,    ...)';
                }
            },
            propCurrent: {
                default() {
                    return false;
                }
            },
        },
        data() {
            return {
                citySearch: '',
                init: false,
            }
        },
        computed: {
            citiesSearch(){
                let cities;
                if(this.citySearch.length > 0 && this.cities.size > 0) {
                    cities = [...this.cities].filter(city => city.name.toLowerCase().indexOf(this.citySearch.toLowerCase()) === 0);
                } else {
                    cities =  [...this.cities];
                }
                let newIndex = 0;
                for(let city of cities){
                    if(+city.id === +this.current.id && !(!this.isShowAll && +city.count === 0)){
                        let index = cities.indexOf(city);
                        if(index !== -1) {
                            cities.splice(newIndex, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            if(city.name.toLowerCase() !== ''){
                                newIndex = 1;
                            }
                        }
                    }
                }
                for(let city of cities){
                    if(city.name.toLowerCase() === '' || city.name.toLowerCase() === '-'){
                        if(city.name.toLowerCase() === '-' && +city.id !== +this.current.id){
                            let index = cities.indexOf(city);
                            if(index !== -1) {
                                cities.splice(newIndex+1, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            }
                        }
                        if(city.name.toLowerCase() === '' && +city.id !== +this.current.id){
                            let index = cities.indexOf(city);
                            if(index !== -1) {
                                cities.splice(newIndex, 0, cities.splice(cities.indexOf(city), 1)[0]);
                            }
                        }
                    }
                }
                return cities;
            },
            current() {
                if(this.propCurrent) {
                    return this.propCurrent
                } else {
                    return this.currentCityState
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                cities: state => state.cities.items,
                currentCityState: state => state.cities.current,
            }),
        },
        mounted(){
            setTimeout(()=>{
                this.init = true
            },100)
        },
        methods: {
            setCity: function (city) {
                if(this.isSet){
                    this.$store.dispatch('cities/setCity', {city: city,closePopup: this.popups.citySelect.name,currentSection: this.$route.params.sid});
                } else {
                    this.$emit('set:city', city);
                }
            }
        }
    }
</script>

